/***
*
*/
export default {
	state:{
		addressData:{},
		addressState:0
	},
	getters:{
		// 
		getAddressData(state){
			return state.addressData
		},
		// 
		getAddressState(state){
			return state.addressState
		},
	},
	mutations:{
		// 
		setAddressData(state,data){
			state.addressData = data;
		},
		// 
		setAddressState(state,data){
			state.addressState = data;
		}
	}
}